.backdrop {
    width: 100%;
    height: 100%;
    background: rgba(255,255,255, 0.9);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    transition: 0.6s;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader {
    width: 75px;
    height: 75px;
    border: 10px solid #0d253f;
    border-radius: 50%;
    border-left: 50%;
    border-left-color: #fff;
    animation: loader 2.0s linear infinite;
}

@keyframes loader {
    100% {
        transform: rotate(360deg);
    }
}